module.exports = "import React from 'react';\nimport { Form, FormText } from 'react-ocean-forms';\n\nfunction Example() {\n  return (\n    <Form>\n      <p>\n        <FormText\n          text=\"Demo output\"\n        />\n      </p>\n\n      <p>\n        <FormText\n          text=\"Output with parameters: {param}\"\n          values={{ param: 'Demo value' }}\n        />\n      </p>\n    </Form>\n  );\n}\n\nexport default Example;\n"