module.exports = "import React from 'react';\nimport { Form, validators } from 'react-ocean-forms';\nimport { Select } from 'react-ocean-forms-bootstrap';\n\nfunction Example({ logMessage }) {\n  // onChange callback\n  const handleChange = (value) => {\n    logMessage('onChange, value: ' + JSON.stringify(value));\n  };\n\n  // onBlur callback\n  const handleBlur = () => {\n    logMessage('onBlur');\n  };\n\n  const options = [\n    { label: 'Option 1', value: \"1\" },\n    { label: 'Option 2', value: \"2\" },\n  ];\n\n  return (\n    <Form className=\"demo\">\n      <Select\n        name=\"myInput\"\n        label=\"Normal input\"\n        onChange={handleChange}\n        onBlur={handleBlur}\n        options={options}\n      />\n\n      <Select\n        name=\"multiInput\"\n        label=\"Multi input\"\n        multi\n        onChange={handleChange}\n        onBlur={handleBlur}\n        options={options}\n      />\n\n      <Select\n        name=\"searchableInput\"\n        label=\"Searchable input\"\n        searchable\n        onChange={handleChange}\n        onBlur={handleBlur}\n        options={options}\n      />\n\n      <Select\n        name=\"clearableInput\"\n        label=\"Clearable input\"\n        clearable\n        onChange={handleChange}\n        onBlur={handleBlur}\n        options={options}\n      />\n\n      <Select\n        name=\"requiredInput\"\n        label=\"Required input\"\n        clearable\n        onChange={handleChange}\n        onBlur={handleBlur}\n        validators={[validators.required]}\n        options={options}\n      />\n    </Form>\n  );\n}\n\nexport default Example;\n"